.chat{
    flex: 0.65;
    display: flex;
    flex-direction: column;
    /* background-color: pink; */
}
.chat__header{
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
}
.chat__headerInfo{
    padding-left: 20px;
    flex: 1;
}
.chat__headerInfo > h3{
    margin-bottom: 3px;
    font-weight: 500;
}
.chat__headerInfo > p{
    color: gray;
}
.chat__headerRight{
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}
.chat__body{
    padding: 30px ;
    flex: 1;
    background: center/contain repeat  url("../whatsapp\ backgroung.png");
    overflow: scroll;
    /* overflow: hidden; */
}

.chat__message{
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: fit-content;
    margin-bottom: 30px;
    border-radius: 10px;
    background-color: #ffffff;
}
.chat__reciever{
    margin-left: auto;
    background-color: #dcf8c6;
}
.chat__name{
    position: absolute;
    top: -15px;
    font-size: xx-small;
    font-weight: 700;
}
.chat__timestamp{
    padding-left: 10px;
    font-size: xx-small;
}
.chat__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
}
.chat__footer > form{
    flex: 1;
    display: flex;
}
.chat__footer > form >input{
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    outline: none;

}
.chat__footer > form > button{
    display: none;
}
.chat__footer > .MuiSvgIcon-root{
    padding: 10px;
    color: gray;
}